// src/components/NavBar.js
import Link from 'next/link';

export default function NavBar() {
    return (
        <nav className="bg-gray-900 text-white py-4 shadow-lg relative">
            <div className="container mx-auto flex items-center justify-between px-6 md:px-10">
                
                {/* Left - Logo */}
                <h1 className="text-3xl font-bold neon-text">BookWorms</h1>
                
                {/* Center - Links */}
                <div className="hidden md:flex space-x-8 text-lg">
                    <Link href="/contact">
                        <a className="hover:text-neon-green transition duration-300">Contact</a>
                    </Link>
                    <Link href="/about">
                        <a className="hover:text-neon-green transition duration-300">About</a>
                    </Link>
                </div>
                
                {/* Right - Buttons */}
                <div className="space-x-4">
                    <button className="bg-transparent border border-neon-green text-neon-green py-2 px-4 rounded hover:bg-neon-green hover:text-gray-900 transition duration-300">
                        Sign In
                    </button>
                    <button className="bg-neon-green text-gray-900 py-2 px-4 rounded hover:bg-transparent hover:text-neon-green border border-neon-green transition duration-300">
                        Sign Up
                    </button>
                </div>
            </div>
            
            {/* Neon Shadow Under Navbar */}
            <div className="absolute inset-x-0 bottom-0 h-1 bg-neon-green shadow-neon-glow"></div>
        </nav>
    );
}
